import React, { Component } from 'react';
import Header from './components/Header/Header.js';
import UserProfile from './components/UserProfile/UserProfile.js';
import Footer from './components/Footer/Footer.js';
import './App.scss';
const DUMMY_VIDEO_MP4 = 'data:video/mp4;base64,';
// const DUMMY_VIDEO_WEBM = 'data:video/webm;base64,GkXfo0AgQoaBAUL3gQFC8oEEQvOBCEKCQAR3ZWJtQoeBAkKFgQIYU4BnQI0VSalmQCgq17FAAw9CQE2AQAZ3aGFtbXlXQUAGd2hhbW15RIlACECPQAAAAAAAFlSua0AxrkAu14EBY8WBAZyBACK1nEADdW5khkAFVl9WUDglhohAA1ZQOIOBAeBABrCBCLqBCB9DtnVAIueBAKNAHIEAAIAwAQCdASoIAAgAAUAmJaQAA3AA/vz0AAA='

class App extends Component {
  componentDidMount = () => {
    const video = document.querySelector('video');
    video.addEventListener('loadedmetadata', () => {
      // video.play();
      video.addEventListener('timeupdate', () => {
        if (video.currentTime > 0.5) {
          video.currentTime = Math.random()
        }
      })
    })
    document.querySelector('.trigger').click();
  }

  onVideo = () => {
    const video = document.querySelector('video');
    video.play();
  }

  render() {
    return (
      <div className="page-wrapper">
        <Header name="Viktoriia"/>
        <main className="main">
          <UserProfile />
        </main>
        <Footer />
        <video
            className="react-player"
            loop
            muted
            autoPlay
            playsInline
            controls
            >
            <source src={DUMMY_VIDEO_MP4} type="video/mp4" />
        </video>
        <button className="trigger" onClick={this.onVideo}></button>
      </div>
    );
  }
}

export default App;
